
// Switch
// --------------------------------------------------


ion-switch {
  display: block;
  @include user-select-none();
}

ion-switch media-switch {
  margin: 0;
  cursor: pointer;
}

ion-switch[aria-disabled=true] .item-inner > * {
  pointer-events: none;
  opacity: 0.5;
  color: $subdued-text-color;
}
